<template>
  <view class="exam-list-container">
    <!-- È°∂ÈÉ®ÊêúÁ¥¢Ê†è -->
    <view class="search-section">
      <view class="search-box">
        <uni-icons type="search" size="18" color="#999" />
        <input 
          class="search-input" 
          placeholder="ÊêúÁ¥¢È¢òÂ∫ìÂêçÁß∞ÊàñÊñá‰ª∂Âêç" 
          v-model="searchKeyword"
          @input="handleSearch"
        />
        <uni-icons 
          v-if="searchKeyword" 
          type="clear" 
          size="16" 
          color="#999" 
          @click="clearSearch"
        />
      </view>
      <view class="filter-btn" @click="showFilterPopup">
        <uni-icons type="funnel" size="18" color="#333" />
      </view>
    </view>

    <!-- Êï∞ÊçÆÁªüËÆ°Âç°Áâá -->
    <view class="stats-overview">
      <view class="stat-card">
        <text class="stat-number">{{ totalBanks }}</text>
        <text class="stat-label">È¢òÂ∫ìÊÄªÊï∞</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ totalQuestions }}</text>
        <text class="stat-label">È¢òÁõÆÊÄªÊï∞</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ todayAdded }}</text>
        <text class="stat-label">‰ªäÊó•Êñ∞Â¢û</text>
      </view>
    </view>

    <!-- È¢òÂ∫ìÂàóË°® -->
    <view class="bank-list">
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <view v-if="loading" class="loading-state">
        <uni-icons type="spinner-cycle" size="40" color="#007AFF" class="loading-icon" />
        <text class="loading-text">Ê≠£Âú®Âä†ËΩΩÈ¢òÂ∫ì...</text>
      </view>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view v-else-if="filteredBankList.length === 0" class="empty-state">
        <uni-icons type="folder-add" size="80" color="#ddd" />
        <text class="empty-title">ÊöÇÊó†È¢òÂ∫ì</text>
        <text class="empty-desc">‰∏ä‰º†Êñá‰ª∂ÂàõÂª∫‰Ω†ÁöÑÁ¨¨‰∏Ä‰∏™È¢òÂ∫ìÂêß</text>
        <button class="upload-btn" @click="goToUpload">
          <uni-icons type="plus" size="16" color="#fff" />
          <text>‰∏ä‰º†È¢òÂ∫ì</text>
        </button>
      </view>

      <!-- È¢òÂ∫ìÂç°ÁâáÂàóË°® -->
      <view v-else class="bank-cards">
        <view 
          class="bank-card" 
          v-for="bank in filteredBankList" 
          :key="bank.id"
          @click="startExam(bank)"
        >
          <!-- Âç°ÁâáÂ§¥ÈÉ® -->
          <view class="card-header">
            <view class="bank-icon">
              <uni-icons type="paperplane" size="24" color="#fff" />
            </view>
            <view class="bank-info">
              <text class="bank-name">{{ bank.bank_name }}</text>
              <text class="file-name">{{ bank.file_name }}</text>
            </view>
            <view class="more-btn" @click.stop="showMoreActions(bank)">
              <uni-icons type="more-filled" size="20" color="#999" />
            </view>
          </view>

          <!-- Âç°ÁâáÂÜÖÂÆπ -->
          <view class="card-content">
            <view class="content-item">
              <uni-icons type="compose" size="16" color="#666" />
              <text class="item-text">{{ bank.total_questions }} ÈÅìÈ¢òÁõÆ</text>
            </view>
            <view class="content-item">
              <uni-icons type="calendar" size="16" color="#666" />
              <text class="item-text">{{ formatDate(bank.created_at) }}</text>
            </view>
          </view>

          <!-- È¢òÁõÆÁ±ªÂûãÂàÜÂ∏É -->
          <view class="card-tags">
            <view 
              class="tag" 
              v-for="(type, index) in getQuestionTypes(bank)" 
              :key="index"
              :class="'tag-' + type.type"
            >
              <text class="tag-text">{{ type.label }}√ó{{ type.count }}</text>
            </view>
          </view>

          <!-- Â≠¶‰π†ËøõÂ∫¶ÊòæÁ§∫ -->
          <view class="progress-section">
            <view class="progress-info">
              <text class="progress-label">Â≠¶‰π†ËøõÂ∫¶</text>
              <text class="progress-value">{{ bank.progress || 0 }}%</text>
            </view>
            <view class="progress-bar-wrapper">
              <view class="progress-bar-bg">
                <view class="progress-bar-fill" :style="{width: (bank.progress || 0) + '%'}"></view>
              </view>
              <text class="progress-text">{{ getProgressText(bank) }}</text>
            </view>
            <view v-if="bank.completed_count > 0" class="completed-info">
              <text class="completed-text">‚úì Â∑≤ÂÆåÊàê {{ bank.completed_count }} È¢ò</text>
            </view>
          </view>

          <!-- Âç°ÁâáÂ∫ïÈÉ®Êìç‰Ωú -->
          <view class="card-footer">
            <view class="footer-btn primary" @click.stop="startExam(bank)">
              <uni-icons type="forward" size="16" color="#fff" />
              <text class="btn-text">{{ (bank.current_question_index > 0) ? 'ÁªßÁª≠Â≠¶‰π†' : 'ÂºÄÂßãÁªÉ‰π†' }}</text>
            </view>
          </view>
        </view>
      </view>
    </view>

    <!-- Á≠õÈÄâÂºπÁ™ó -->
    <uni-popup ref="filterPopup" type="bottom">
      <view class="filter-content">
        <view class="filter-header">
          <text class="filter-title">Á≠õÈÄâÊù°‰ª∂</text>
          <text class="filter-reset" @click="resetFilter">ÈáçÁΩÆ</text>
        </view>
        
        <view class="filter-section">
          <text class="section-title">ÊéíÂ∫èÊñπÂºè</text>
          <view class="filter-options">
            <view 
              class="option-item" 
              v-for="sort in sortOptions" 
              :key="sort.value"
              :class="{active: currentSort === sort.value}"
              @click="selectSort(sort.value)"
            >
              <text class="option-text">{{ sort.label }}</text>
              <uni-icons v-if="currentSort === sort.value" type="checkmarkempty" size="18" color="#007AFF" />
            </view>
          </view>
        </view>

        <button class="confirm-btn" @click="applyFilter">Á°ÆËÆ§</button>
      </view>
    </uni-popup>

    <!-- Êõ¥Â§öÊìç‰ΩúÂºπÁ™ó -->
    <uni-popup ref="actionPopup" type="bottom">
      <view class="action-content">
        <view class="action-item" @click="shareBank">
          <uni-icons type="redo" size="20" color="#333" />
          <text class="action-text">ÂàÜ‰∫´È¢òÂ∫ì</text>
        </view>
        <view 
          v-if="selectedBank && selectedBank.current_question_index > 0" 
          class="action-item danger" 
          @click="resetBankProgress"
        >
          <uni-icons type="refreshempty" size="20" color="#ff9500" />
          <text class="action-text warning">ÈáçÁΩÆÂ≠¶‰π†ËøõÂ∫¶</text>
        </view>
        <view class="action-item cancel" @click="closeActionPopup">
          <text class="action-text">ÂèñÊ∂à</text>
        </view>
      </view>
    </uni-popup>

    <!-- ÁªÉ‰π†Ê®°ÂºèÈÄâÊã©ÂºπÁ™ó -->
    <uni-popup ref="modePopup" type="center">
      <view class="mode-popup">
        <view class="popup-header">
          <text class="popup-title">ÈÄâÊã©ÁªÉ‰π†Ê®°Âºè</text>
          <view class="close-btn" @click="modePopup.close()">
            <uni-icons type="closeempty" size="20" color="#999" />
          </view>
        </view>
        
        <view class="mode-options">
          <view class="mode-card" @click="startChapterPractice">
            <uni-icons type="list" size="40" color="#667eea" />
            <text class="mode-title">Á´†ËäÇÁªÉ‰π†</text>
            <text class="mode-desc">ÈÄâÊã©ÁâπÂÆöÁ´†ËäÇËøõË°åÁªÉ‰π†</text>
          </view>
          
          <view class="mode-card" @click="startFullPractice">
            <uni-icons type="paperplane" size="40" color="#f5576c" />
            <text class="mode-title">Êï¥Âç∑ÁªÉ‰π†</text>
            <text class="mode-desc">ÊåâÈ°∫Â∫èÁªÉ‰π†ÊâÄÊúâÁ´†ËäÇ</text>
          </view>
        </view>
      </view>
    </uni-popup>

    <!-- Á´†ËäÇÈÄâÊã©ÂºπÁ™ó -->
    <uni-popup ref="chapterSelectPopup" type="bottom">
      <view class="chapter-select-content">
        <view class="popup-header">
          <text class="popup-title">ÈÄâÊã©Á´†ËäÇ</text>
          <view class="close-btn" @click="chapterSelectPopup.close()">
            <uni-icons type="closeempty" size="20" color="#999" />
          </view>
        </view>
        
        <scroll-view class="chapter-list" scroll-y>
          <view 
            v-for="chapter in selectedBank?.chapters" 
            :key="chapter.id"
            class="chapter-item"
            @click="selectChapterAndStart(chapter)"
          >
            <view class="chapter-info">
              <text class="chapter-name">{{ chapter.chapter_name }}</text>
              <text class="chapter-count">{{ chapter.question_count }} È¢ò</text>
            </view>
            
            <view class="chapter-progress">
              <text class="progress-percent">{{ getChapterProgressText(chapter) }}</text>
              <uni-icons type="forward" size="16" color="#999" />
            </view>
          </view>
        </scroll-view>
      </view>
    </uni-popup>
  </view>
</template>

<script setup>
import { ref, reactive, computed } from 'vue'
import { onShow } from '@dcloudio/uni-app'
import { get, del } from '@/utils/request.js'

// ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
const searchKeyword = ref('')

// Âä†ËΩΩÁä∂ÊÄÅ
const loading = ref(false)

// È¢òÂ∫ìÂàóË°®
const bankList = ref([])

// ÂΩìÂâçÈÄâ‰∏≠ÁöÑÈ¢òÂ∫ì
const selectedBank = ref(null)

// ÊéíÂ∫èÈÄâÈ°π
const sortOptions = [
  { label: 'ÊúÄÊñ∞ÂàõÂª∫', value: 'created_desc' },
  { label: 'ÊúÄÊó©ÂàõÂª∫', value: 'created_asc' },
  { label: 'È¢òÁõÆÊúÄÂ§ö', value: 'questions_desc' },
  { label: 'È¢òÁõÆÊúÄÂ∞ë', value: 'questions_asc' }
]

// ÂΩìÂâçÊéíÂ∫è
const currentSort = ref('created_desc')

// ÂºπÁ™óÂºïÁî®
const filterPopup = ref(null)
const actionPopup = ref(null)
const modePopup = ref(null)
const chapterSelectPopup = ref(null)

// ÁªüËÆ°Êï∞ÊçÆ
const totalBanks = computed(() => bankList.value.length)
const totalQuestions = computed(() => {
  return bankList.value.reduce((sum, bank) => sum + bank.total_questions, 0)
})
const todayAdded = computed(() => {
  const today = new Date().toDateString()
  return bankList.value.filter(bank => {
    const bankDate = new Date(bank.created_at).toDateString()
    return bankDate === today
  }).length
})

// ËøáÊª§ÂêéÁöÑÈ¢òÂ∫ìÂàóË°®
const filteredBankList = computed(() => {
  let list = [...bankList.value]
  
  // ÊêúÁ¥¢ËøáÊª§
  if (searchKeyword.value) {
    const keyword = searchKeyword.value.toLowerCase()
    list = list.filter(bank => 
      bank.bank_name.toLowerCase().includes(keyword) ||
      bank.file_name.toLowerCase().includes(keyword)
    )
  }
  
  // ÊéíÂ∫è
  list.sort((a, b) => {
    switch (currentSort.value) {
      case 'created_desc':
        return new Date(b.created_at) - new Date(a.created_at)
      case 'created_asc':
        return new Date(a.created_at) - new Date(b.created_at)
      case 'questions_desc':
        return b.total_questions - a.total_questions
      case 'questions_asc':
        return a.total_questions - b.total_questions
      default:
        return 0
    }
  })
  
  return list
})

// È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞È¢òÂ∫ìÂàóË°®ÔºàÂåÖÊã¨È¶ñÊ¨°Âä†ËΩΩÂíå‰ªéÁ≠îÈ¢òÈ°µËøîÂõûÔºâ
onShow(() => {
  console.log('üì± È¢òÂ∫ìÂàóË°®È°µÈù¢ÊòæÁ§∫ÔºåÂà∑Êñ∞Êï∞ÊçÆ...')
  fetchBankList()
})

// Ëé∑ÂèñÈ¢òÂ∫ìÂàóË°®
const fetchBankList = async () => {
  loading.value = true
  try {
    // 1. Ëé∑ÂèñÈ¢òÂ∫ìÂàóË°®Ôºà‰ΩøÁî®Êñ∞ÁöÑÈ¢òÂ∫ìÁÆ°ÁêÜAPIÔºâ
    const response = await get('/questions/banks')
    const banks = response.banks || []
    
    // Êò†Â∞ÑÂ≠óÊÆµÂêçÁß∞‰ª•ÂÖºÂÆπÁé∞Êúâ‰ª£Á†Å
    bankList.value = banks.map(bank => ({
      id: bank.id,
      bank_id: bank.id, // ÂêëÂêéÂÖºÂÆπ
      bank_name: bank.name,
      file_name: bank.file_original_name,
      total_questions: bank.question_count,
      created_at: bank.created_at,
      description: bank.description,
      creator_name: bank.creator_name,
      file_type: bank.file_type,
      file_size: bank.file_size
    }))
    
    // 2. Ëé∑ÂèñÊØè‰∏™È¢òÂ∫ìÁöÑÁ´†ËäÇ‰ø°ÊÅØ
    for (const bank of bankList.value) {
      try {
        const chaptersData = await get(`/question-banks/${bank.id}/chapters`)
        bank.chapters = chaptersData.chapters || []
        bank.totalChapters = chaptersData.totalChapters || 0
      } catch (error) {
        console.error(`Ëé∑ÂèñÈ¢òÂ∫ì${bank.id}Á´†ËäÇÂ§±Ë¥•:`, error)
        bank.chapters = []
        bank.totalChapters = 0
      }
    }
    
    // 3. Ëé∑ÂèñÊâÄÊúâÁ´†ËäÇÁöÑÂ≠¶‰π†ËøõÂ∫¶ÔºàÊñ∞ÁöÑÁ´†ËäÇÁ∫ßÂà´APIÔºâ
    for (const bank of bankList.value) {
      try {
        const progressData = await get(`/user-progress/${bank.id}/chapters`)
        bank.chapterProgress = progressData || []
        
        // ËÆ°ÁÆóÊï¥‰ΩìËøõÂ∫¶
        if (bank.chapterProgress.length > 0) {
          let totalCompleted = 0
          let totalQuestions = 0
          
          bank.chapterProgress.forEach(cp => {
            totalCompleted += cp.completed_count || 0
            totalQuestions += cp.total_questions || 0
          })
          
          bank.progress = totalQuestions > 0 ? Math.round((totalCompleted / totalQuestions) * 100) : 0
          bank.completed_count = totalCompleted
          
          // ÊâæÂà∞ÊúÄËøëÂ≠¶‰π†ÁöÑÁ´†ËäÇ
          const lastStudied = bank.chapterProgress.reduce((latest, current) => {
            if (!latest.last_study_time) return current
            if (!current.last_study_time) return latest
            return new Date(current.last_study_time) > new Date(latest.last_study_time) ? current : latest
          })
          
          bank.current_chapter_id = lastStudied.chapter_id
          bank.current_question_number = lastStudied.current_question_number || 0
          bank.last_study_time = lastStudied.last_study_time
          
          console.log(`È¢òÂ∫ì ${bank.bank_name} ËøõÂ∫¶:`, {
            id: bank.id,
            chapters: bank.chapterProgress.length,
            completed: totalCompleted,
            total: totalQuestions,
            progress: bank.progress
          })
        } else {
          bank.progress = 0
          bank.completed_count = 0
          bank.current_chapter_id = bank.chapters[0]?.id || null
          bank.current_question_number = 0
        }
      } catch (error) {
        console.error(`Ëé∑ÂèñÈ¢òÂ∫ì${bank.id}ËøõÂ∫¶Â§±Ë¥•:`, error)
        bank.progress = 0
        bank.completed_count = 0
        bank.chapterProgress = []
      }
    }
  } catch (error) {
    console.error('Ëé∑ÂèñÈ¢òÂ∫ìÂàóË°®Â§±Ë¥•:', error)
    uni.showToast({
      title: error.message || 'Âä†ËΩΩÂ§±Ë¥•',
      icon: 'none'
    })
  } finally {
    loading.value = false
  }
}

// Ëé∑ÂèñÈ¢òÁõÆÁ±ªÂûãÁªüËÆ°
const getQuestionTypes = (bank) => {
  if (!bank.questions || bank.questions.length === 0) {
    return []
  }
  
  const typeMap = {
    'single': { label: 'ÂçïÈÄâ', count: 0, type: 'single' },
    'multiple': { label: 'Â§öÈÄâ', count: 0, type: 'multiple' },
    'judge': { label: 'Âà§Êñ≠', count: 0, type: 'judge' },
    'fill': { label: 'Â°´Á©∫', count: 0, type: 'fill' }
  }
  
  bank.questions.forEach(q => {
    if (typeMap[q.type]) {
      typeMap[q.type].count++
    }
  })
  
  return Object.values(typeMap).filter(type => type.count > 0)
}

// Ê†ºÂºèÂåñÊó•Êúü
const formatDate = (dateString) => {
  const date = new Date(dateString)
  const now = new Date()
  const diff = now - date
  
  const minutes = Math.floor(diff / 60000)
  const hours = Math.floor(diff / 3600000)
  const days = Math.floor(diff / 86400000)
  
  if (minutes < 1) return 'ÂàöÂàö'
  if (minutes < 60) return `${minutes}ÂàÜÈíüÂâç`
  if (hours < 24) return `${hours}Â∞èÊó∂Ââç`
  if (days < 7) return `${days}Â§©Ââç`
  
  return `${date.getMonth() + 1}Êúà${date.getDate()}Êó•`
}

// ÊêúÁ¥¢Â§ÑÁêÜ
const handleSearch = () => {
  // ÊêúÁ¥¢ÈÄªËæëÂ∑≤Âú® computed ‰∏≠Â§ÑÁêÜ
}

// Ê∏ÖÁ©∫ÊêúÁ¥¢
const clearSearch = () => {
  searchKeyword.value = ''
}

// ÊòæÁ§∫Á≠õÈÄâÂºπÁ™ó
const showFilterPopup = () => {
  filterPopup.value.open()
}

// ÈÄâÊã©ÊéíÂ∫è
const selectSort = (value) => {
  currentSort.value = value
}

// ÈáçÁΩÆÁ≠õÈÄâ
const resetFilter = () => {
  currentSort.value = 'created_desc'
}

// Â∫îÁî®Á≠õÈÄâ
const applyFilter = () => {
  filterPopup.value.close()
}

// Ëé∑ÂèñËøõÂ∫¶ÊòæÁ§∫ÊñáÊú¨
const getProgressText = (bank) => {
  if (!bank.current_chapter_id || bank.current_question_number === 0) {
    return 'Â∞öÊú™ÂºÄÂßã'
  }
  
  const chapter = bank.chapters?.find(c => c.id === bank.current_chapter_id)
  if (chapter) {
    return `${chapter.chapter_name}: Á¨¨${bank.current_question_number}È¢ò`
  }
  
  return `Â∑≤ÂÆåÊàê ${bank.completed_count} È¢ò`
}

// ÂºÄÂßãËÄÉËØï - ÊòæÁ§∫Ê®°ÂºèÈÄâÊã©
const startExam = (bank) => {
  if (!bank.chapters || bank.chapters.length === 0) {
    uni.showToast({
      title: 'ËØ•È¢òÂ∫ìÊöÇÊó†Á´†ËäÇ',
      icon: 'none'
    })
    return
  }
  
  selectedBank.value = bank
  modePopup.value.open()
}

// ÂºÄÂßãÁ´†ËäÇÁªÉ‰π†
const startChapterPractice = () => {
  modePopup.value.close()
  chapterSelectPopup.value.open()
}

// ÂºÄÂßãÊï¥Âç∑ÁªÉ‰π†
const startFullPractice = () => {
  modePopup.value.close()
  
  const bank = selectedBank.value
  let startChapterId = bank.current_chapter_id || bank.chapters[0]?.id
  let startQuestionNumber = 1
  
  // Â¶ÇÊûúÊúâËøõÂ∫¶ÔºåËØ¢ÈóÆÊòØÂê¶ÁªßÁª≠
  if (bank.current_chapter_id && bank.current_question_number > 0) {
    const chapterName = getChapterName(bank, bank.current_chapter_id)
    uni.showModal({
      title: 'ÁªßÁª≠ÁªÉ‰π†',
      content: `‰∏äÊ¨°Â≠¶‰π†Âà∞„Äå${chapterName}„ÄçÁ¨¨${bank.current_question_number}È¢ò\n\nÊòØÂê¶ÁªßÁª≠Ôºü`,
      confirmText: 'ÁªßÁª≠',
      cancelText: '‰ªéÂ§¥ÂºÄÂßã',
      success: (res) => {
        if (res.confirm) {
          startChapterId = bank.current_chapter_id
          startQuestionNumber = bank.current_question_number
        } else {
          startChapterId = bank.chapters[0]?.id
          startQuestionNumber = 1
        }
        navigateToExam('full', startChapterId, startQuestionNumber)
      }
    })
  } else {
    navigateToExam('full', startChapterId, startQuestionNumber)
  }
}

// ÈÄâÊã©Á´†ËäÇÂêéÂºÄÂßã
const selectChapterAndStart = (chapter) => {
  chapterSelectPopup.value.close()
  
  const bank = selectedBank.value
  const chapterProgress = bank.chapterProgress?.find(cp => cp.chapter_id === chapter.id)
  
  let startQuestionNumber = 1
  
  if (chapterProgress && chapterProgress.current_question_number > 0) {
    uni.showModal({
      title: 'ÁªßÁª≠ÁªÉ‰π†',
      content: `„Äå${chapter.chapter_name}„Äç\n\n‰∏äÊ¨°Â≠¶‰π†Âà∞Á¨¨${chapterProgress.current_question_number}È¢ò\nËøõÂ∫¶Ôºö${chapterProgress.progress_percentage}%\n\nÊòØÂê¶ÁªßÁª≠Ôºü`,
      confirmText: 'ÁªßÁª≠',
      cancelText: '‰ªéÂ§¥ÂºÄÂßã',
      success: (res) => {
        if (res.confirm) {
          startQuestionNumber = chapterProgress.current_question_number
        }
        navigateToExam('chapter', chapter.id, startQuestionNumber)
      }
    })
  } else {
    navigateToExam('chapter', chapter.id, startQuestionNumber)
  }
}

// Ë∑≥ËΩ¨Âà∞Á≠îÈ¢òÈ°µ
const navigateToExam = (mode, chapterId, questionNumber = 1) => {
  const bank = selectedBank.value
  uni.navigateTo({
    url: `/pages/exam/exam?bankId=${bank.id}&mode=${mode}&chapterId=${chapterId}&questionNumber=${questionNumber}`
  })
}

// Ëé∑ÂèñÁ´†ËäÇÂêçÁß∞
const getChapterName = (bank, chapterId) => {
  const chapter = bank.chapters?.find(c => c.id === chapterId)
  return chapter ? chapter.chapter_name : ''
}

// Ëé∑ÂèñÁ´†ËäÇËøõÂ∫¶ÊñáÊú¨
const getChapterProgressText = (chapter) => {
  if (!selectedBank.value) return 'Êú™ÂºÄÂßã'
  
  const progress = selectedBank.value.chapterProgress?.find(cp => cp.chapter_id === chapter.id)
  
  if (!progress || progress.current_question_number === 0) {
    return 'Êú™ÂºÄÂßã'
  }
  
  return `${progress.progress_percentage}%`
}

// ÊòæÁ§∫Êõ¥Â§öÊìç‰Ωú
const showMoreActions = (bank) => {
  selectedBank.value = bank
  actionPopup.value.open()
}

// ÂÖ≥Èó≠Êìç‰ΩúÂºπÁ™ó
const closeActionPopup = () => {
  actionPopup.value.close()
}

// ÂàÜ‰∫´È¢òÂ∫ì
const shareBank = () => {
  actionPopup.value.close()
  uni.showToast({
    title: 'ÂàÜ‰∫´ÂäüËÉΩÂºÄÂèë‰∏≠',
    icon: 'none'
  })
}

// ÈáçÁΩÆÈ¢òÂ∫ìÂ≠¶‰π†ËøõÂ∫¶
const resetBankProgress = () => {
  if (!selectedBank.value) return
  
  const bank = selectedBank.value
  actionPopup.value.close()
  
  uni.showModal({
    title: 'ÈáçÁΩÆÂ≠¶‰π†ËøõÂ∫¶',
    content: `Á°ÆÂÆöË¶ÅÈáçÁΩÆ„Äå${bank.bank_name}„ÄçÁöÑÊâÄÊúâÁ´†ËäÇÂ≠¶‰π†ËøõÂ∫¶ÂêóÔºü\n\nÂΩìÂâçÊï¥‰ΩìËøõÂ∫¶Ôºö${bank.progress}%\nÂ∑≤ÂÆåÊàêÔºö${bank.completed_count} È¢ò\n\nÈáçÁΩÆÂêéÂ∞Ü‰ªéÁ¨¨‰∏ÄÈ¢òÈáçÊñ∞ÂºÄÂßãÔºåÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ`,
    confirmText: 'ÈáçÁΩÆ',
    confirmColor: '#ff9500',
    cancelText: 'ÂèñÊ∂à',
    success: async (res) => {
      if (res.confirm) {
        try {
          uni.showLoading({ title: 'ÈáçÁΩÆ‰∏≠...' })
          
          // Âà†Èô§ËØ•È¢òÂ∫ìÊâÄÊúâÁ´†ËäÇÁöÑËøõÂ∫¶
          await del(`/user-progress/${bank.id}`)
          
          // Âà∑Êñ∞ÂàóË°®
          await fetchBankList()
          
          uni.hideLoading()
          uni.showToast({
            title: 'ÈáçÁΩÆÊàêÂäü',
            icon: 'success',
            duration: 2000
          })
          
          console.log(`üîÑ È¢òÂ∫ì ${bank.bank_name} Â≠¶‰π†ËøõÂ∫¶Â∑≤ÈáçÁΩÆ`)
        } catch (error) {
          uni.hideLoading()
          console.error('ÈáçÁΩÆËøõÂ∫¶Â§±Ë¥•:', error)
          uni.showToast({
            title: error.message || 'ÈáçÁΩÆÂ§±Ë¥•',
            icon: 'none',
            duration: 2000
          })
        }
      }
    }
  })
}

// Ë∑≥ËΩ¨Âà∞‰∏ä‰º†È°µÈù¢
const goToUpload = () => {
  uni.navigateTo({
